<template>
  <tas-base-crud :config="config" hide-create hide-update hide-detail hide-delete>
    <template v-slot:list-is_bpjs_tambahan="data">
      <div v-if="data && data.rowData" class="form-group mb-0 row align-items-center">
        <div class="col-3">
          <span class="switch switch-icon">
            <label>
              <input :disabled="!$_sys.isAllowed(config.permission.update)" @change="updateTambahanBPJS(data.rowData)" v-model="data.rowData.is_bpjs_tambahan" type="checkbox" checked="checked" name="select" :true-value="true" :false-value="false">
              <span></span>
            </label>
          </span>
        </div>
      </div>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-bpjs-kes-tambahan-employee-families',
  data () {
    return {
      config: {
        title: 'Tambahan Keluarga BPJS Kesehatan',
        // model_api: 'bpjs-kes-tambahan-employee-families',
        getter: 'custom/bpjs-kes-tambahan-employee-families',
        setter: 'bpjs-kes-tambahan-employee-families',
        pk_field: null,
        permission: {
          create: 'create-bpjs-kes-tambahan-employee-families',
          read: 'view-bpjs-kes-tambahan-employee-families',
          update: 'update-bpjs-kes-tambahan-employee-families',
          delete: 'delete-bpjs-kes-tambahan-employee-families'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Keterangan', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          { id: 'fullname', label: 'Nama Keluarga', methods: { list: true } },
          { id: 'rel_family_relationship_id', label: 'Hubungan', methods: { list: true } },
          {
            id: 'is_bpjs_tambahan',
            label: 'BPJS Tambahan',
            methods: {
              list: {
                type: 'row-slot'
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              }
            }
          }
          //   { id: 'prosen_premi_company', label: 'Premi Perusahaan (%)', methods: { list: { transform: 'prosen' }, detail: { transform: 'prosen' }, create: { type: 'number' }, update: { type: 'number' } } },
          //   { id: 'prosen_premi_employee', label: 'Premi Karyawan (%)', methods: { list: { transform: 'prosen' }, detail: { transform: 'prosen' }, create: { type: 'number' }, update: { type: 'number' } } },
          //   { id: 'periode_month', label: 'Periode', methods: { list: { transform: 'period' }, detail: { transform: 'period' }, create: { type: 'month' }, update: { type: 'month' }, filter: false } },
          //   { id: 'upah_max_value', label: 'Upah Maksimal', methods: { list: { transform: 'money' }, detail: { transform: 'money' }, create: { type: 'money' }, update: { type: 'money' }, filter: false } },
          //   { id: 'upah_min_value', label: 'Upah Minimal', methods: { list: { transform: 'money' }, detail: { transform: 'money' }, create: { type: 'money' }, update: { type: 'money' }, filter: false } }
        ]
      }
    }
  },
  methods: {
    updateTambahanBPJS (e) {
      this.$_api.put('custom/bpjs-kes-tambahan-employee-families/update', e).then(res => {
        const stats = !res.data.is_bpjs_tambahan ? 'menonaktifkan' : 'mengaktifkan'
        this.$_alert.success('Berhasil ' + stats + ' BPJS Tambahan untuk ' + e.fullname)
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



